







































































import { Component, Vue } from 'vue-property-decorator';
import VueBarcode from 'vue-barcode';
import { Printd } from 'printd';
import { mapState } from 'vuex';
import { printBarcodeStyles } from "../util/printBarStyle";
import DeleteDialog from '@/components/DeleteBarcodeDialog.vue';
import SnackBar from '@/components/SnackBar.vue';


@Component({
  components: {
    VueBarcode,
    DeleteDialog,
    SnackBar
  },
  computed: {
    ...mapState(["barcodes"])
  }
})
export default class Print extends Vue {
  // Mapped Variables ----------------
  // ---------------------------------
  barcodes!: any;

  // Local Variables -----------------
  // ---------------------------------
  dialog: boolean = false;
  barData: object = {};
  snackInit: boolean = false;
  delText: boolean = false;
  window: any = {
    width: 0,
    height: 0
  }

  // Computed ------------------------
  // ---------------------------------
  get cardWidth() {
    return this.window.width >= 3000 ? "500" : "344";
  }

  get barcodeWidth() {
    return this.window.width <= 440 ? "1" : "2";
  }

  get conditionDisplay() {
    return this.barcodes.length > 0 ? true : false;
  }

  get barcodeFontSize() {
    if (this.window.width >= 4096) return "50"
    if (this.window.width >= 3840 && this.window.width <= 4095) return "40"
    if (this.window.width >= 2560 && this.window.width <= 3839) return "30"
    return "20"
  }

  // Lifecycle Events ----------------
  // ---------------------------------
  created() {
    this.$store.dispatch("retrieveBarcodes");
    window.addEventListener('resize', this.handleResize)
    this.handleResize();
  }

  destroyed() {
    window.removeEventListener('resize', this.handleResize)
  }

  // Methods -------------------------
  // ---------------------------------
  closeParentD() {
    this.dialog = false;
  }

  deleteSnack() {
    this.snackInit = true;
      setTimeout(() => {
        this.snackInit = false
      }, 2000);
  }

  printBarcode(index) {
    const d = new Printd();
    d.print(document.getElementById(`${index}`), [ printBarcodeStyles ]);
  }

  deleteBarcode(bar) {
    this.dialog = true;
    this.barData = bar;
    this.delText = true;
  }
  
  handleResize() {
    this.window.width = window.innerWidth;
    this.window.height = window.innerHeight;
  }
}
